<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1">疫苗管理系统注册</h1>
                                                                  <el-form-item v-if="tableName=='yuangong'" label="工号" prop="yuangonggonghao">
        <el-input v-model="ruleForm.gonghao" placeholder="工号"></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="姓名" prop="yuangongxingming">
        <el-input v-model="ruleForm.xingming" placeholder="姓名"></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="密码" prop="yuangongmima">
        <el-input v-model="ruleForm.mima" placeholder="密码"></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="年龄" prop="yuangongnianling">
        <el-input v-model="ruleForm.nianling" placeholder="年龄"></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="职称" prop="yuangongzhicheng">
        <el-input v-model="ruleForm.zhicheng" placeholder="职称"></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="联系电话" prop="yuangonglianxidianhua">
        <el-input v-model="ruleForm.lianxidianhua" placeholder="联系电话"></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="身份证" prop="yuangongshenfenzheng">
        <el-input v-model="ruleForm.shenfenzheng" placeholder="身份证"></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="家庭住址" prop="yuangongjiatingzhuzhi">
        <el-input v-model="ruleForm.jiatingzhuzhi" placeholder="家庭住址"></el-input>
      </el-form-item>
                                          <el-button @click="login()" class="btn-login" type="primary">注册</el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 注册
    login() {
                                                                                                                                                                                                                                                                                                                                    if(`yuangong` == this.tableName && this.ruleForm.lianxidianhua&&(!this.$validate.isMobile(this.ruleForm.lianxidianhua))){
        this.$message.error(`联系电话应输入电话格式`);
        return
      }
                                                                        if(`yuangong` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(`身份证应输入身份证格式`);
        return
      }
                                                                                                this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
