<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
      <el-row>
                  <el-col :span="12">
        <el-form-item v-if="type!='info'" label="疫苗编号" prop="yimiaobianhao">
            <el-input v-model="ruleForm.yimiaobianhao" 
                placeholder="疫苗编号" readonly></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.yimiaobianhao" label="疫苗编号" prop="yimiaobianhao">
              <el-input v-model="ruleForm.yimiaobianhao" 
                placeholder="疫苗编号" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="疫苗名称" prop="yimiaomingcheng">
          <el-input v-model="ruleForm.yimiaomingcheng" 
              placeholder="疫苗名称" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.yimiaomingcheng" label="疫苗名称" prop="yimiaomingcheng">
              <el-input v-model="ruleForm.yimiaomingcheng" 
                placeholder="疫苗名称" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="type!='info'"  label="疫苗类型" prop="yimiaoleixing">
          <el-select v-model="ruleForm.yimiaoleixing" placeholder="请选择疫苗类型">
            <el-option
                v-for="(item,index) in yimiaoleixingOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.yimiaoleixing" label="疫苗类型" prop="yimiaoleixing">
              <el-input v-model="ruleForm.yimiaoleixing" 
                placeholder="疫苗类型" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="库存数量" prop="kucunshuliang">
          <el-input v-model="ruleForm.kucunshuliang" 
              placeholder="库存数量" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.kucunshuliang" label="库存数量" prop="kucunshuliang">
              <el-input v-model="ruleForm.kucunshuliang" 
                placeholder="库存数量" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="接种年龄" prop="jiezhongnianling">
          <el-input v-model="ruleForm.jiezhongnianling" 
              placeholder="接种年龄" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.jiezhongnianling" label="接种年龄" prop="jiezhongnianling">
              <el-input v-model="ruleForm.jiezhongnianling" 
                placeholder="接种年龄" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                                    </el-row>
                                                                                                                                                                                        <el-row>
            <el-col :span="24">
              <el-form-item v-if="type!='info'"  label="详情" prop="xiangqing">
                <editor 
                    style="min-width: 200px; max-width: 600px;"
                    v-model="ruleForm.xiangqing" 
                    class="editor" 
                    action="file/upload">
                </editor>
              </el-form-item>
              <div v-else>
                <el-form-item v-if="ruleForm.xiangqing" label="详情" prop="xiangqing">
                    <span v-html="ruleForm.xiangqing"></span>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
                    <el-form-item>
                <el-button v-if="type!='info'" type="primary" @click="onSubmit">提交</el-button>
        <el-button v-if="type!='info'" @click="back()">取消</el-button>
        <el-button v-if="type=='info'" @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
  data() {
    var validateIdCard = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!checkIdCard(value)) {
        callback(new Error("请输入正确的身份证号码"));
      } else {
        callback();
      }
    };
    var validateUrl = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isURL(value)) {
        callback(new Error("请输入正确的URL地址"));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isMobile(value)) {
        callback(new Error("请输入正确的电话号码"));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isEmail(value)) {
        callback(new Error("请输入正确的邮箱地址"));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isNumber(value)) {
        callback(new Error("请输入数字"));
      } else {
        callback();
      }
    };
    var validateIntNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isIntNumer(value)) {
        callback(new Error("请输入整数"));
      } else {
        callback();
      }
    };
    return {
      id: '',
      type: '',
      ruleForm: {
                        yimiaobianhao: this.getUUID(),
                                yimiaomingcheng: '',
                                yimiaoleixing: '',
                                kucunshuliang: '',
                                jiezhongnianling: '',
                                xiangqing: '',
                      },
                                                    yimiaoleixingOptions: [],
                                                              rules: {
                  yimiaobianhao: [
                                                                                              ],
                  yimiaomingcheng: [
                                                                                              ],
                  yimiaoleixing: [
                                                                                              ],
                  kucunshuliang: [
                                        { validator: validateIntNumber, trigger: 'blur' },
                                                                                  ],
                  jiezhongnianling: [
                                                                                              ],
                  xiangqing: [
                                                                                              ],
              }
    };
  },
  props: ["parent"],
  computed: {
                                                                              },
  methods: {
    // 下载
    download(file){
      window.open(`${file}`)
    },
    // 初始化
    init(id,type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if(this.type=='info'||this.type=='else'){
        this.info(id);
      }else if(this.type=='cross'){
        var obj = this.$storage.getObj('crossObj');
        for (var o in obj){
                    if(o=='yimiaobianhao'){
            this.ruleForm.yimiaobianhao = obj[o];
            continue;
          }
                    if(o=='yimiaomingcheng'){
            this.ruleForm.yimiaomingcheng = obj[o];
            continue;
          }
                    if(o=='yimiaoleixing'){
            this.ruleForm.yimiaoleixing = obj[o];
            continue;
          }
                    if(o=='kucunshuliang'){
            this.ruleForm.kucunshuliang = obj[o];
            continue;
          }
                    if(o=='jiezhongnianling'){
            this.ruleForm.jiezhongnianling = obj[o];
            continue;
          }
                    if(o=='xiangqing'){
            this.ruleForm.xiangqing = obj[o];
            continue;
          }
                  }
                                                                                                              }
                                                                              this.$http({
              url: `option/yimiaoleixing/yimiaoleixing`,
              method: "get"
            }).then(({ data }) => {
              if (data && data.code === 0) {
                this.yimiaoleixingOptions = data.data;
              } else {
                this.$message.error(data.msg);
              }
            });
         
                                                                                  },
                                                        // 多级联动参数
                                                                        info(id) {
      this.$http({
        url: `yimiaomingcheng/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.ruleForm = data.data;
        } else {
          this.$message.error(data.msg);
        }
      });
    },
        // 提交
    onSubmit() {
                                                                                                                                                                  this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `yimiaomingcheng/${!this.ruleForm.id ? "save" : "update"}`,
            method: "post",
            data: this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "操作成功",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.parent.showFlag = true;
                  this.parent.addOrUpdateFlag = false;
                  this.parent.yimiaomingchengCrossAddOrUpdateFlag = false;
                  this.parent.search();
                }
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.yimiaomingchengCrossAddOrUpdateFlag = false;
    },
                                                                              }
};
</script>
<style lang="scss" scoped>
.editor{
  height: 500px;
}
</style>
